@import "General.scss";

form{
    display: flex;
    flex-direction: column;
    background-color: $dark-violet;
    background-image: url("../images/bg-shorten-mobile.svg");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    padding: 2.5rem 1.5rem;
    border-radius: .75rem;
    width: 75%;
    margin: 0 auto;
    top: -8rem;

    @media screen and (min-width: 640px){
        padding: 2.5rem;
    }
    @media screen and (min-width:1024px){
        flex-direction: row;
        justify-content: center;
        background-image: url("../images/bg-shorten-desktop.svg");
        width: 70%;
        top: -6.5rem;
        
    }
    @media screen and (min-width: 1280px){
        width: 60%;
    }

    input{
        font-size: 1.25rem;
        border-radius: .5rem;
        padding: 1rem;
        margin-bottom: .75rem;

        @media screen and (min-width: 640px){
            border-radius: .75rem;
        }
        @media screen and (min-width: 1024px){
            width: 75%;
            margin: 0;
        }
        @media screen and (min-width: 1200px){
            margin:0 .5rem 0 0;
            font-size: 1.375rem;
        }
    }
    button.btn{
        border-radius: .5rem;
        margin-top: .75rem;

        @media screen and (min-width: 640px){
            border-radius: .75rem;
        }
        @media screen and (min-width: 1024px){
            margin:0 0 0 .5rem;
            width: 25%;
            padding: 0;
        }
    }
}